import React, {
  useContext,
  useDeferredValue,
  useEffect,
  useId,
  useMemo,
  useState,
} from "react";
import { useDispatch, useSelector } from "react-redux";
import {
  cleanup,
  getAllTables,
  loader,
  tables,
} from "../../redux/slices/AllTablesSlice";

import { FaCircle } from "react-icons/fa6";
import { CiSearch } from "react-icons/ci";
import FilteredTable from "./FilteredTable";

import { FaLongArrowAltLeft } from "react-icons/fa";
import { useNavigate } from "react-router-dom";
import tableContext from "../../context/tableContext";

const Table = () => {
  const [SearchValue, setSearchValue] = useState("");
  const deferredValue = useDeferredValue(SearchValue);
  const navigate = useNavigate();
  const alltables = useSelector(tables);
  const [Tables, setTables] = useState([]);
  const { categoryId } = useContext(tableContext);
  const Loader = useSelector(loader);
  const dispatch = useDispatch();
  const id = useId();

  useEffect(() => {
    dispatch(getAllTables());

    return () => dispatch(cleanup());
  }, []);

  useEffect(() => {
    setTables((state) => [...alltables]);
  }, [alltables]);

  useMemo(() => {
    const filtered = alltables.filter((tab) => tab.GroupId === categoryId);
    setTables((state) => [...filtered]);
  }, [categoryId]);

  useMemo(() => {
    const filtered = alltables.filter(
      (tab) => tab.Tablename.toLowerCase() === deferredValue.toLowerCase()
    );
    setTables((state) => [...filtered]);
  }, [deferredValue]);

  const navigateBack = () => {
    navigate("/home");
  };
  return (
    <>
      {loader === "loading" && (
        <Loader type="TailSpin" color="orange" height={100} width={100} />
      )}

      <div className=" col-start-1 col-end-12 border-r border-slate-300  bg-slate-100 px-16 py-4">
        <div className="container">
          <div className="border-b border-slate-300 flex justify-between items-center ">
            <div className="inline-flex items-center gap-3">
              <span
                className=" inline-block p-2  text-slate-500 cursor-pointer hover:bg-white hover:rounded-lg "
                onClick={navigateBack}
              >
                <FaLongArrowAltLeft></FaLongArrowAltLeft>
              </span>

              <CiSearch />
              <input
                type="text"
                className=" outline-none pl-2 bg-transparent text-orange-500"
                placeholder="search table.."
                onChange={(e) => setSearchValue(e.target.value)}
              />
            </div>
            <ul className=" text-slate-500 flex justify-end items-center gap-3 text-sm font-light ">
              <li key={`${id}1`} className=" inline-flex items-center gap-2">
                <FaCircle className="text-blue-600 ring-blue-300 ring-2 rounded-full text-sm" />
                Available
              </li>
              <li key={`${id}2`} className=" inline-flex items-center gap-2">
                <FaCircle className="text-yellow-600 ring-yellow-300 ring-2 rounded-full text-sm" />
                Available Soon
              </li>
              <li key={`${id}3`} className=" inline-flex items-center gap-2">
                <FaCircle className="text-orange-600 ring-2 ring-orange-300 rounded-full text-sm" />
                Occupied
              </li>
            </ul>
          </div>
          <FilteredTable alltables={Tables} />
        </div>
      </div>
    </>
  );
};

export default Table;
