import React, { useContext, useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { getAllGroups, groups } from "../../redux/slices/GroupSlice";
import tableContext from "../../context/tableContext";

const TablesideBar = (props) => {
  const Groups = useSelector(groups);
  const dispatch = useDispatch();
  const { setCategoryId, categoryId } = useContext(tableContext);

  useEffect(() => {
    dispatch(getAllGroups());
  }, []);

  return (
    <>
      <div className=" col-start-12 col-end-auto">
        <div className="px-2">
          <ul className="text-center py-2 px-0 md:px-2 lg:px-2 cursor-pointer ">
            {Groups.length === 0 && (
              <p className=" text-slate-500 text-center font-medium text-xl">
                No category Found
              </p>
            )}
            {Groups.map((group) => {
              if (group.inactive === 0) {
                return (
                  <li
                    key={group.id}
                    className={
                      categoryId === group.id
                        ? "sm:px-2 sm:py-3 md:px-2 md:py-4 transition ease-in-out delay-300  text-base md:text-lg rounded-full bg-orange-500 text-white mb-2"
                        : "sm:px-2 sm:py-3 md:px-2 md:py-4 transition ease-in-out delay-300  text-base md:text-lg rounded-full bg-orange-300 text-white mb-2  hover:bg-orange-500"
                    }
                    onClick={() => setCategoryId(group.id)}
                  >
                    {group.Groupname}
                  </li>
                );
              }
            })}
          </ul>
        </div>
      </div>
    </>
  );
};

export default TablesideBar;
