import React, { useEffect } from "react";
import t4 from "../../assets/T-4.svg";
import t2 from "../../assets/T-2.svg";
import t10 from "../../assets/T-10.svg";
import { FaCircle } from "react-icons/fa6";
import { addedActions } from "../../redux/slices/addedOrderSlice";
import { useDispatch, useSelector } from "react-redux";
import { Toaster, toast } from "react-hot-toast";
import { confirmAlert } from "react-confirm-alert";
import "react-confirm-alert/src/react-confirm-alert.css";
import {
  status,
  code,
  updateTableStatus,
  cleanup,
} from "../../redux/slices/AllTablesSlice";

const FilteredTable = ({ alltables }) => {
  const dispatch = useDispatch();
  const Code = useSelector(code);
  const Status = useSelector(status);

  useEffect(() => {
    if (Code === 200) {
      if (Status === "updated") {
        toast.success(`Table status is Available Now`);
      }
    } else if (Code === 500 || Code === 404) {
      toast.error("Something Wrong, try again!");
    }

    return () => dispatch(cleanup());
  }, [Code, Status]);

  const tableSelectionHandler = (id, name, status) => {
    if (status === "O") {
      toast.error("Unable to select Occupied Table");
    } else if (status === "S") {
      toast.error(
        "This table will be Available soon, Try other available table"
      );
    } else {
      dispatch(
        addedActions.updateTableDetails({
          tableId: id,
          tableName: name,
        })
      );

      toast.success(`Table ${name} is selected`);
    }
  };

  const makeitAvailable = (id) => {
    confirmAlert({
      title: "Confirm",
      message: "Are you sure you want to make it available",
      buttons: [
        {
          label: "Yes",
          onClick: () => {
            dispatch(updateTableStatus(id));
          },
        },
        {
          label: "No",
          onClick: () => {
            toast("cancelling the process, still you can do it later");
          },
        },
      ],
    });
  };
  return (
    <>
      <Toaster />
      <div className="grid grid-cols-4 mt-3 gap-5">
        {alltables.length === 0 ? (
          <p className=" text-slate-500 text-center font-medium text-xl">
            No Tables Found...
          </p>
        ) : (
          alltables.map((table, index) => {
            let color = "text-blue-600";
            color =
              table.status === "S"
                ? "text-yellow-600"
                : table.status === "O"
                ? "text-orange-600"
                : "text-blue-600";

            if (table.capacity === 2 && table.inactive === 0) {
              return (
                <div
                  key={index}
                  className=" flex justify-center relative border p-2 rounded-xl cursor-pointer hover:border hover:border-orange-500"
                  onClick={
                    table.status !== "S"
                      ? () =>
                          tableSelectionHandler(
                            table.id,
                            table.Tablename,
                            table.status
                          )
                      : () => makeitAvailable(table.id)
                  }
                >
                  <img src={t2} alt={table.Tablename} />
                  <p className=" absolute top-1/2 left-1/2 -translate-x-1/2 -translate-y-1/2 text-lg">
                    {table.Tablename}
                  </p>
                  <span className="absolute top-2 right-3">
                    <FaCircle className={color} />
                  </span>
                </div>
              );
            }
            if (table.capacity === 4 && table.inactive === 0) {
              return (
                <div
                  key={index}
                  className=" flex justify-center relative  border p-2 rounded-xl cursor-pointer hover:border hover:border-orange-500"
                  onClick={
                    table.status !== "S"
                      ? () =>
                          tableSelectionHandler(
                            table.id,
                            table.Tablename,
                            table.status
                          )
                      : () => makeitAvailable(table.id)
                  }
                >
                  <img src={t4} alt={table.Tablename} />
                  <p className=" absolute top-1/2 left-1/2 -translate-x-1/2 -translate-y-1/2 text-lg">
                    {table.Tablename}
                  </p>
                  <span className="absolute top-2 right-3">
                    <FaCircle className={color} />
                  </span>
                </div>
              );
            }
            if (table.capacity === 10 && table.inactive === 0) {
              return (
                <div
                  key={index}
                  className=" flex justify-center relative  border p-2 rounded-xl cursor-pointer hover:border hover:border-orange-500"
                  onClick={
                    table.status !== "S"
                      ? () =>
                          tableSelectionHandler(
                            table.id,
                            table.Tablename,
                            table.status
                          )
                      : () => makeitAvailable(table.id)
                  }
                >
                  <img src={t10} alt={table.Tablename} />
                  <p className=" absolute top-1/2 left-1/2 -translate-x-1/2 -translate-y-1/2 text-lg">
                    {table.Tablename}
                  </p>
                  <span className="absolute top-2 right-3">
                    <FaCircle className={color} />
                  </span>
                </div>
              );
            }

            return null;
          })
        )}
      </div>
    </>
  );
};

export default FilteredTable;
